<template>
  <div class="modal-document">
    <div v-if="lang===`pl`">
      <h4 class="fw-bold">Korpus Polski wchodzi do gry</h4>
      <p>
        Uwolnieni z sowieckich więzień i łagrów Polacy wstępowali w szeregi polskiej armii, która stała się zaczątkiem
        największej jednostki Polskich Sił Zbrojnych na Zachodzie. Żołnierze pod dowództwem gen. Władysława Andersa
        wsławili się, zdobywając m.in Monte Cassino, Ankonę i Bolonię.
        „Pociąg zatrzymał się na stacji Buzułuku, wyjrzałem przez okno i ku swojemu zdumieniu zobaczyłem żołnierza w
        polskim mundurze. Wyskoczyłem z wagonu i od tego kaprala dowiedziałem się, że trwa nabór do polskiego wojska w
        ZSRR” – tak o swojej drodze do 2 Korpusu Polskiego opowiada podpułkownik Marian Tomaszewski, żołnierz 6 Pułku
        Pancernego „Dzieci Lwowskich”. Pod koniec 1939 roku aresztowali go Sowieci i zesłali do Kazachstanu. Pracował w
        kopalni węgla w Karagandzie, skąd po napaści Niemiec na ZSRR w 1941 roku udało mu się uciec. Gdy został żołnierzem
        gen. Andersa, miał 19 lat.
      </p>
      <h4 class="fw-bold"> Od Syberii po Egipt</h4>
      <p>
        „2 Korpus Polski powstał na mocy rozkazu naczelnego wodza gen. Władysława Sikorskiego z 29 czerwca 1943 roku” –
        przypomina dr Janusz Kowalewski, historyk zajmujący się dziejami Polskich Sił Zbrojnych na Zachodzie. Utworzenie
        jednostki poprzedziło zawarcie 30 lipca 1941 roku porozumienia Sikorski-Majski, na mocy którego rozpoczęto w ZSRR
        formowanie Armii Polskiej na Wschodzie. W jej szeregi ściągali Polacy uwolnieni z łagrów i obozów sowieckich.
        Wobec narastającego konfliktu z władzami sowieckimi, w 1942 roku żołnierzy ewakuowano ze Związku Radzieckiego.
        „Nieludzką ziemię” opuściło wówczas 116,5 tys. polskich obywateli, w tym ponad 78 tys. żołnierzy. Powstała w ZSRR
        Armia Polska stała się główną siłą utworzonego 21 lipca 1943 roku w Iraku 2 Korpusu, największej jednostki
        Polskich Sił Zbrojnych na Zachodzie. Na jej czele stanął generał Władysław Anders.
        W skład korpusu weszły 5 i 6 Dywizja Piechoty, z których sformowano 5 Kresową Dywizję Piechoty. Dołączono do nich
        Samodzielną Brygadę Strzelców Karpackich, powołaną w 1940 roku na Bliskim Wschodzie. Stała się ona podstawą 3
        Dywizji Strzelców Karpackich. 2 Korpus tworzyła też 2 Grupa Artylerii, 2 Warszawska Brygada Pancerna oraz mniejsze
        oddziały. „Razem z ojcem postanowiliśmy zgłosić się do armii w 1942 roku. Otrzymałem wtedy przydział do 6
        Batalionu«Dzieci Lwowskich», przemianowanego potem na 6 Pułk Pancerny «Dzieci Lwowskich» 2 Warszawskiej Brygady
        Pancernej – opowiada major OttonHulacki. W szeregach 2 Korpusu jako podchorąży brał udział w najważniejszych
        bitwach pod wodzą gen. Andersa.
        W sumie jesienią 1943 roku siły korpusu liczyły prawie 53 tys. żołnierzy, w tym ponad 3 tys. oficerów. „Jednostka
        została podporządkowana brytyjskiej 9 Armii, której dowództwo wyznaczyło termin osiągnięcia przez korpus gotowości
        na 1 stycznia 1944 roku” – mówi historyk. Na początku polskie oddziały ochraniały pola naftowe w północnym Iraku.
        Potem żołnierzy przeniesiono do Palestyny, gdzie przeszli szkolenie w górach, a następnie do Egiptu, gdzie
        otrzymali nowe uzbrojenie i wyposażenie.
      </p>
      <h4 class="fw-bold">Na włoskiej ziemi</h4>
      <p>
        W grudniu 1943 roku rozpoczął się transport wojsk i sprzętu 2 Korpusu na południe Włoch. Jednym z pierwszych zadań
        Polaków na Półwyspie Apenińskim było zdobycie wzgórza i ufortyfikowanego klasztoru Monte Cassino, które zamykały
        drogę do Rzymu. „Żołnierze! Długo czekaliśmy na tę chwilę odwetu i zemsty nad odwiecznym naszym wrogiem. Zadanie,
        które nam przypadło rozsławi na cały świat imię żołnierza polskiego”, napisał w rozkazie generał Anders.
        „Należałem do batalionu szturmowego, chcieliśmy z kolegami dorwać Niemców, więc kiedy ruszył szturm, zapanowała
        euforia” – wspomina porucznik Władysław Dąbrowski z 15 Pułku Ułanów Poznańskich. Po sześciu dniach walki, 18 maja
        1944 roku polskie oddziały zdobyły klasztor, przełamując niemieckie umocnienia tzw. linii Gustawa. „To były dni,
        których nie zapomnę do końca życia” przyznaje porucznik Dąbrowski.
        Kolejnym polskim sukcesem w kampanii włoskiej było zdobycie 18 lipca Ankony na wybrzeżu Morza Adriatyckiego.
        „Opanowanie tego portu zapewniło dostawy wojskom alianckim i ułatwiło prowadzenie dalszych walk z Niemcami” –
        wyjaśnia dr Kowalewski.
        Natomiast wiosną 1945 roku 2 Korpus brał udział w bitwie o Bolonię, tocząc walki z trzema doborowymi dywizjami
        niemieckimi. Mimo to 21 kwietnia Polacy jako pierwsi wkroczyli do miasta. Dowódca 8 Armii brytyjskiej generał Mac
        Creery napisał do żołnierzy: „W tej wielkiej bitwie wykazaliście wspaniałego ducha walki, wytrwałość i sprawność
        bojową”.
      </p>

      <h4 class="fw-bold">Wolna Polska</h4>
      <p>
        Walka o Bolonię była ostatnią batalią 2 Korpusu, który po zakończeniu wojny pozostał we Włoszech w składzie wojsk
        okupacyjnych. Jednocześnie w jego szeregi wstępowali Polacy zwalniani z obozów i przymusowych robót. Latem 1945
        roku korpus liczył ponad 100 tys. żołnierzy.
        Po wojnie część z nich, przeważnie szeregowych, zdecydowała się na powrót do kraju. W grudniu 1945 roku do Polski
        przyjechało ponad 12 tys. żołnierzy, potem wracali kolejni. Wśród nich por. Dąbrowski, który dowiedział się, że
        jego matka i brat przeżyli zsyłkę na Wschód i dotarli do Polski.
        „Wielu z powracających było represjonowanych i prześladowanych przez komunistyczne władze” – mówi historyk.
        Większość żołnierzy, m.in. podpułkownik Tomaszewski i major Hulacki, została jednak na Zachodzie i latem 1946 roku
        przetransportowano ich do Wielkiej Brytanii. W 1947 roku 2 Korpus Polski ostatecznie został rozwiązany.
        Ponad 4 tys. wojskowych, którzy polegli podczas walk na Półwyspie Apenińskim, spoczywa na czterech polskich
        cmentarzach we Włoszech: na Monte Cassino, w Bolonii, Loreto oraz na południu kraju w Casamassima. Wśród nich, na
        Monte Cassino, zgodnie ze swoją wolą został pochowany gen. Anders, który po wojnie zamieszkał w Londynie i tam
        zmarł w 1970 roku.
        „2 Korpus był dla jego żołnierzy drugą rodziną, szansą na wyjście z nieludzkiej sowieckiej ziemi, a jednocześnie
        dawał możliwość walki za wolną Polskę, której niestety wielu z nich nie doczekało” – mówi senator Anna Anders,
        córka generała.
      </p>

    </div>
    <div v-else-if="lang===`en`">
            <h4 class="fw-bold">The Polish Corps comes into play</h4>
      <p>
        Released from Soviet prisons and labor camps, Poles joined the ranks of the Polish army, which became the nucleus of the largest unit of the Polish Armed Forces in the West. The soldiers under the command of General Władysław Anders became famous, winning, among others, Monte Cassino, Ancona and Bologna. “The train stopped at the Buzułuku station, I looked out the window and to my amazement I saw a soldier in a Polish uniform. I jumped out of the wagon and from this corporal I found out that there was a recruitment to the Polish army in the USSR "- this is how Lieutenant Colonel Marian Tomaszewski, a soldier of the 6th Armored Regiment" Lviv Children "tells about his way to the 2nd Polish Corps. At the end of 1939, the Soviets arrested him and sent him to Kazakhstan. He worked in a coal mine in Karaganda, from where he managed to escape after the German invasion of the USSR in 1941. When he became a soldier of General Anders, he was 19 years old.
      </p>
      <h4 class="fw-bold"> From Siberia to Egypt</h4>
      <p>
        "The 2nd Polish Corps was established by order of the Commander-in-Chief, General Władysław Sikorski on June 29, 1943," reminds Dr. Janusz Kowalewski, a historian dealing with the history of the Polish Armed Forces in the West. The establishment of the unit was preceded by the conclusion of the Sikorski-Majski Agreement on July 30, 1941, under which the formation of the Polish Army in the East began in the USSR. Its ranks were drawn by Poles freed from Soviet labor camps and camps. In view of the growing conflict with the Soviet authorities, in 1942 the soldiers were evacuated from the Soviet Union. The "inhuman land" left then 116.5 thousand. Polish citizens, including over 78,000 soldiers. The Polish Army established in the USSR became the main force of the 2nd Corps, the largest unit of the Polish Armed Forces in the West, created on July 21, 1943 in Iraq. It was headed by General Władysław Anders. The corps included the 5th and 6th Infantry Divisions, from which the 5th Kresowa Infantry Division was formed. They were joined by the Independent Carpathian Rifle Brigade, established in 1940 in the Middle East. It became the basis of the 3rd Carpathian Rifle Division. The 2nd Corps also consisted of the 2nd Artillery Group, the 2nd Warsaw Armored Brigade and smaller units. “Together with my father, we decided to join the army in 1942. I was then assigned to the 6th "Lviv Children" Battalion, later renamed the 6th Armored Regiment "Children of Lviv" of the 2nd Warsaw Armored Brigade - says Major OttonHulacki. As an officer cadet in the ranks of the 2nd Corps, he took part in the most important battles led by General Anders. In total, in the fall of 1943, the forces of the corps numbered almost 53,000. soldiers, including over 3,000 officers. "The unit was subordinated to the British 9th Army, whose command set the deadline for achieving readiness by the corps on January 1, 1944," says the historian. Initially, Polish troops protected the oil fields in northern Iraq. After that, the soldiers were transferred to Palestine, where they underwent training in the mountains, and then to Egypt, where they received new weapons and equipment.
      </p>
      <h4 class="fw-bold">On Italian soil</h4>
      <p>
        In December 1943, the transport of troops and equipment of the 2nd Corps to the south of Italy began. One of the first tasks of Poles on the Apennine Peninsula was to conquer the hill and the fortified monastery of Monte Cassino, which blocked the road to Rome. "Soldiers! We have waited a long time for this moment of revenge and vengeance on our eternal enemy. The task that has been assigned to us will make the name of the Polish soldier known to the whole world, ”General Anders wrote in the order. "I belonged to an assault battalion, we wanted to catch the Germans with my friends, so when the assault started, there was euphoria" - remembers Lieutenant Władysław Dąbrowski from the 15th Poznań Uhlans Regiment. After six days of fighting, on May 18, 1944, Polish troops captured the monastery, breaking the German fortifications of the so-called the Gustav line. "Those were days that I will not forget for the rest of my life," admits Lieutenant Dąbrowski. Another Polish success in the Italian campaign was the capture of Ancona on the coast of the Adriatic Sea on 18 July. "The control of this port provided supplies to the Allied troops and facilitated further fights with Germany" - explains Dr. Kowalewski. In the spring of 1945, the 2nd Corps took part in the Battle of Bologna, fighting against three selected German divisions. Nevertheless, on April 21, Poles were the first to enter the city. The commander of the British 8th Army, General Mac Creery wrote to the soldiers: "In this great battle you showed great fighting spirit, endurance and combat prowess."
      </p>

      <h4 class="fw-bold">Free Poland</h4>
      <p>
        The fight for Bologna was the last battle of the 2nd Corps, which remained in Italy as part of the occupation forces after the end of the war. At the same time, its ranks were joined by Poles released from camps and forced labor. In the summer of 1945, the corps numbered over 100,000. soldiers. After the war, some of them, mostly privates, decided to return to Poland. In December 1945, over 12 thousand people came to Poland. soldiers, then others returned. Among them, lieutenant Dąbrowski, who learned that his mother and brother had survived the exile to the East and reached Poland. "Many of the returnees were repressed and persecuted by the communist authorities," says the historian. Most of the soldiers, incl. Lieutenant Colonel Tomaszewski and Major Hulacki, however, stayed in the West and in the summer of 1946 they were transported to Great Britain. In 1947, the 2nd Polish Corps was finally disbanded. Over 4,000 soldiers who died during the fighting on the Apennine Peninsula are buried in four Polish cemeteries in Italy: Monte Cassino, Bologna, Loreto and in the south of the country in Casamassima. Among them, in Monte Cassino, according to his will, General Anders, who lived in London after the war and died there in 1970, was buried there. "The 2nd Corps was a second family for his soldiers, a chance to leave the inhuman Soviet land, and at the same time gave the opportunity to fight for a free Poland, which, unfortunately, many of them did not live to see" - says senator Anna Anders, the general's daughter.
      </p>
    </div>


    <div v-else-if="lang===`it`">
            <h4 class="fw-bold">Entra in gioco il Corpo Polacco</h4>
      <p>
        Liberati dalle prigioni e dai campi di lavoro sovietici, i polacchi si unirono ai ranghi dell'esercito polacco, che divenne il nucleo della più grande unità delle forze armate polacche in Occidente. I soldati al comando del generale Władysław Anders divennero famosi, conquistando, tra gli altri, Monte Cassino, Ancona e Bologna. “Il treno si è fermato alla stazione di Buzułuku, ho guardato fuori dal finestrino e con mio grande stupore ho visto un soldato in uniforme polacca. Sono saltato giù dal carro e da questo caporale ho scoperto che c'era un reclutamento nell'esercito polacco in URSS "- così racconta la sua strada il tenente colonnello Marian Tomaszewski, un soldato del 6 ° reggimento corazzato" Lviv Children " al 2 ° Corpo d'Armata Polacco. Alla fine del 1939, i sovietici lo arrestarono e lo mandarono in Kazakistan. Lavorò in una miniera di carbone a Karaganda, da dove riuscì a fuggire dopo l'invasione tedesca dell'URSS nel 1941. Quando divenne un soldato del generale Anders, aveva 19 anni.
      </p>
      <h4 class="fw-bold"> Dalla Siberia all'Egitto</h4>
      <p>
        "Il 2 ° corpo polacco è stato istituito per ordine del comandante in capo, generale Władysław Sikorski il 29 giugno 1943", ricorda il dottor Janusz Kowalewski, uno storico che si occupa della storia delle forze armate polacche in Occidente. L'istituzione dell'unità fu preceduta dalla conclusione dell'Accordo Sikorski-Majski il 30 luglio 1941, in base al quale iniziò la formazione dell'esercito polacco in Oriente nell'URSS. I suoi ranghi furono redatti da polacchi liberati dai campi di lavoro e dai campi sovietici. In considerazione del crescente conflitto con le autorità sovietiche, nel 1942 i soldati furono evacuati dall'Unione Sovietica. La "terra disumana" ha lasciato allora 116,5 mila. Cittadini polacchi, inclusi oltre 78.000 soldati. L'esercito polacco stabilito in URSS divenne la forza principale del 2 ° Corpo, la più grande unità delle forze armate polacche in Occidente, creata il 21 luglio 1943 in Iraq. Era diretto dal generale Władysław Anders. Il corpo comprendeva la 5a e la 6a divisione di fanteria, da cui fu formata la 5a divisione di fanteria di Kresowa. A loro si unì la Brigata Indipendente dei Fucilieri dei Carpazi, fondata nel 1940 in Medio Oriente. Divenne la base della 3a Divisione Fucilieri dei Carpazi. Il 2 ° Corpo era composto anche dal 2 ° Gruppo di artiglieria, dalla 2 ° Brigata corazzata di Varsavia e da unità più piccole. “Insieme a mio padre, decidemmo di arruolarci nell'esercito nel 1942. Fui quindi assegnato al 6 ° battaglione" Lviv Children ", in seguito ribattezzato 6 ° reggimento corazzato" Children of Lviv "della 2a brigata corazzata di Varsavia - dice il maggiore OttonHulacki. Come ufficiale cadetto nelle file del 2 ° Corpo, prese parte alle battaglie più importanti guidate dal generale Anders. In totale, nell'autunno del 1943, le forze del corpo erano quasi 53.000. soldati, compresi oltre 3.000 ufficiali. "L'unità era subordinata alla 9a armata britannica, il cui comando fissò la scadenza per raggiungere la prontezza del corpo il 1 gennaio 1944", dice lo storico. Inizialmente, le truppe polacche hanno protetto i giacimenti petroliferi nel nord dell'Iraq. Successivamente, i soldati sono stati trasferiti in Palestina, dove sono stati addestrati in montagna, e poi in Egitto, dove hanno ricevuto nuove armi e attrezzature.


      </p>
      <h4 class="fw-bold">Sul suolo italiano</h4>
      <p>
        Nel dicembre 1943 iniziò il trasporto delle truppe e degli equipaggiamenti del 2 ° Corpo d'armata nel sud dell'Italia. Uno dei primi compiti dei polacchi sulla penisola appenninica fu quello di conquistare il colle e il monastero fortificato di Monte Cassino, che bloccavano la strada per Roma. "Soldati! Abbiamo atteso a lungo questo momento di vendetta e vendetta sul nostro eterno nemico. Il compito che ci è stato assegnato farà conoscere il nome del soldato polacco a tutto il mondo", ha scritto il generale Anders nell'ordine . "Facevo parte di un battaglione d'assalto, volevamo catturare i tedeschi con i miei amici, quindi quando è iniziato l'assalto, c'era euforia" - ricorda il tenente Władysław Dąbrowski del 15 ° reggimento Uhlans di Poznań. Dopo sei giorni di combattimenti, il 18 maggio , 1944, le truppe polacche conquistarono il monastero, rompendo le fortificazioni tedesche della cosiddetta linea Gustav. "Erano giorni che non dimenticherò per il resto della mia vita", ammette il tenente Dąbrowski. Un altro successo polacco nella campagna italiana fu la presa di Ancona, sulla costa del Mar Adriatico, il 18 luglio. "Il controllo di questo porto forniva rifornimenti alle truppe alleate e facilitava ulteriori scontri con la Germania" - spiega il dottor Kowalewski. Nella primavera del 1945, il 2 ° Corpo d'armata ha preso parte nella battaglia di Bologna, combattendo contro tre divisioni tedesche selezionate. Tuttavia, il 21 aprile, i polacchi furono i primi ad entrare in città. Il comandante dell'8a armata britannica, il generale Mac Creery scrisse ai soldati: "In questa grande battaglia avete mostrato grande spirito combattivo, resistenza e abilità nel combattimento".
      </p>

      <h4 class="fw-bold">Polonia libera</h4>
      <p>
        
La lotta per Bologna fu l'ultima battaglia del 2 ° Corpo d'armata, che rimase in Italia come parte delle forze di occupazione dopo la fine della guerra. Allo stesso tempo, le sue fila furono raggiunte da polacchi rilasciati dai campi e dai lavori forzati. Nell'estate del 1945, il corpo era di oltre 100.000. soldati. Dopo la guerra, alcuni di loro, per lo più privati, decisero di tornare in Polonia. Nel dicembre 1945 vennero in Polonia oltre 12mila persone. soldati, poi altri sono tornati. Tra loro, il tenente Dąbrowski, che ha appreso che sua madre e suo fratello erano sopravvissuti all'esilio in Oriente e avevano raggiunto la Polonia. "Molti dei rimpatriati sono stati repressi e perseguitati dalle autorità comuniste", dice lo storico. La maggior parte dei soldati, incl. Il tenente colonnello Tomaszewski e il maggiore Hulacki, tuttavia, rimasero in Occidente e nell'estate del 1946 furono trasportati in Gran Bretagna. Nel 1947, il 2 ° corpo polacco fu finalmente sciolto. Oltre 4.000 soldati morti durante i combattimenti sulla penisola appenninica sono sepolti in quattro cimiteri polacchi in Italia: Monte Cassino, Bologna, Loreto e nel sud del Paese a Casamassima. Tra questi, a Montecassino, secondo la sua volontà, vi fu sepolto il generale Anders, che visse a Londra nel dopoguerra e vi morì nel 1970. "Il 2 ° Corpo era una seconda famiglia per i suoi soldati, un'opportunità per lasciare la disumana terra sovietica, e allo stesso tempo ha dato l'opportunità di combattere per una Polonia libera, che, purtroppo, molti di loro non hanno vissuto abbastanza" - dice la senatrice Anna Anders, la figlia del generale.

      </p>
    </div>
  </div>
